export default {
	props: {
		type:{
			type: String,
			default: 'fadeInMiddle'
		},
		tapMaskHide: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			showBl: false
		}
	},
	methods: {
		show() {
			this.showBl = true;
		},
		maskHide() {
			if(this.tapMaskHide) this.hide();
		},
		hide() {
			this.showBl = false;
		},
		voidFc() {}
	}
}
